import React, {Component} from 'react';
import './App.css';
import HeaderBar from './HeaderBar'
import Section from "./Section";
import Topic from "./Topic";
import {basic_information, contacts, experience, technologies, theme} from "./Resume";

class App extends Component {



    constructor() {
        super();
        this.state = {
            theme: theme,
            techExpanded: false
        };
    }

    updateTheme = (newTheme) => {
        this.setState({
            theme: newTheme
        })
    };

    render() {
        return (
            <div>
                <HeaderBar
                    name={basic_information.name}
                    mobile={basic_information.phone}
                    email={basic_information.email}
                />
                <div id="sidebar">
                    <Section title={"Objective"}>
                        <Topic>{basic_information.objective}</Topic>
                    </Section>
                    <Section title={"Technologies"}>
                        <Topic>
                            <ul>
                                {technologies.map((x) => {
                                    let showLong = this.state.techExpanded;
                                    return (
                                        <li key={x.long}>

                                            {showLong ? x.long : x.short}
                                            {showLong &&
                                            <ul>
                                                {x.frameworks.map((y) => {
                                                    return (
                                                        <li key={y}>{y}</li>
                                                    )
                                                })}
                                            </ul>
                                            }
                                        </li>
                                    )
                                })}
                            </ul>
                        </Topic>
                    </Section>
                    <Section title={"Find Me"}>
                        <Topic>
                            {contacts.map((v) => {
                                return <div key={v.url}>
                                    {v.icon}
                                    <a
                                        href={v.url}>{v.url}
                                    </a>
                                </div>
                            })}
                        </Topic>
                    </Section>
                    <Section title={"References"}>
                        <Topic>
                            <div> {basic_information.reference_policy} </div>
                        </Topic>
                    </Section>
                </div>


                <div id="contentbar">
                    {
                        Object.keys(experience).map((key) => {
                            return <Section title={key} key={key}>
                                {experience[key].map((role) => <Topic headers={role.headline} key={role.headline.join("")}>
                                        {role.short}
                                    </Topic>
                                )}
                            </Section>
                        })
                    }
                </div>
            </div>

        )
    }
}

export default App;



